import React, { useCallback, useEffect, useState } from 'react';
import { useDropzone } from 'react-dropzone';
import { connect, useDispatch } from 'react-redux';
import { first } from 'rxjs/operators';
import { setAuthenticatedUser } from '../../../reduxStore/actions/authActions';
import { updateUserById, updateUserProfilePicture, changeUsersPassword } from '../../../services/user.service';
import { APP_NAME } from '../../../shared/utility';
import { Button,  UnfocusableWrapper } from '../../common';
import HeaderTopBar from '../../common/HeaderTopBar/HeaderTopBar';
import cls from './MyProfile.module.scss';
import { isMobile } from 'react-device-detect';
import { setShowMenu, setShowSideMenu } from '../../../reduxStore/actions/flowActions';
import { useLocation, useNavigate } from 'react-router-dom';

const MyPassword = props => {
	const { authenticatedUser } = props;
	const [, setAvatarError] = useState('');
	const [authenticUser, setAuthenticUser] = useState(authenticatedUser);

	const [actualPassword, setActualPassword] = useState('');
	const [actualPasswordError, setActualPasswordError] = useState('');
	const [newPassword, setNewPassword] = useState('');
	const [confirmNewPassword, setConfirmNewPassword] = useState('');
	const [confirmationError, setConfirmationError] = useState('');
	
	const [loading, setLoading] = useState(false);
	const [, setShowTasks] = useState(false);
	const [showResetSuccessModal, setShowResetSuccessModal] = useState(false);
	const location = useLocation();
	const data = location.state;

	document.title = `My Profile | ${APP_NAME}`;

	const navigate = useNavigate();

	const dispatch = useDispatch();

	const onDrop = useCallback(
		acceptedFiles =>
			acceptedFiles.forEach(file => {
				setAvatarError('');
				if (file?.type?.toString().indexOf('image/') > -1 && ['jpeg', 'jpg', 'png'].indexOf(file?.type?.toString().split('/')[1]) > -1) {
					let reader = new FileReader();
					reader.readAsDataURL(file);
					reader.onloadend = _e => {
						const formData = new FormData();
						formData.append('avatar', file);
						updateUserProfilePicture(authenticUser.id, file).subscribe({
							next: data => {
								setAuthenticUser(data);
							},
							error: error => {
								setAvatarError(error?.data);
							},
						});
					};
				}
			}),
		[]
	);
	const { getRootProps, getInputProps, isDragActive } = useDropzone({ onDrop });

	const [authenticUser_ApiError, setAuthenticUser_ApiError] = useState('');
	const [updateUser_apiError, setUpdateUser_ApiError] = useState('');

	useEffect(() => {
		setAuthenticUser_ApiError('');

		if (data?.user?.id.length > 0) {
			setAuthenticUser(data.user);
			dispatch(setShowMenu(false));
		}
		
		dispatch(setShowSideMenu(false));
	}, []);

	const [fieldsErrors, setFieldErrors] = useState({ first_name: '', last_name: '', mobile: '', address:'', city: '', postal:'' });
	const [elementsTouched, setElementsTouched] = useState({ first_name: false, last_name: false, mobile: false, address: false, city: false, postal: false });

	const onSaveUserData = useCallback(() => {
		if (!Object.values(elementsTouched).some(value => value === true)) {
			return;
		}
		setFieldErrors({ first_name: '', last_name: '', mobile: '', address:'', city: '', postal:'' });
		setUpdateUser_ApiError('');

		
		updateUserById(authenticUser.id, authenticUser.first_name, authenticUser.last_name, authenticUser.phone, authenticUser.address, authenticUser.city, authenticUser.postal )
			.pipe(first())
			.subscribe({
				next: () => {
					setElementsTouched({ first_name: false, last_name: false, mobile: false, address: false, city: false, postal: false });
				},
				error: error => {
					const foundErrors = { first_name: '', last_name: '', mobile: '', address:'', city: '', postal:'' };
					if (error.data.errors.first_name) {
						foundErrors.first_name = error.data.errors.first_name?.join('; ');
					}
					if (error.data.errors.last_name) {
						foundErrors.last_name = error.data.errors.last_name?.join('; ');
					}
					if (error.data.errors.mobile) {
						foundErrors.mobile = error.data.errors.mobile?.join('; ');
					}
					if (error.data.errors.address) {
						foundErrors.address = error.data.errors.address?.join('; ');
					}
					if (error.data.errors.city) {
						foundErrors.city = error.data.errors.city?.join('; ');
					}
					if (error.data.errors.postal) {
						foundErrors.postal = error.data.errors.postal?.join('; ');
					}
					if (error.data.message !== 'The given data was invalid.') {
						setUpdateUser_ApiError(error.data.message);
					}
					setFieldErrors(c => ({ ...c, ...foundErrors }));
				},
			});
	}, [authenticUser, elementsTouched]);
	

	const onSubmitClick = () => {
		setLoading(true);
		if (newPassword !== confirmNewPassword) {
			setLoading(false);
			return setConfirmationError('Confirmation does not match');
		}
		changeUsersPassword(actualPassword, newPassword, confirmNewPassword, authenticUser.id)
			.pipe(first())
			.subscribe({
				next: () => {
					setLoading(false);
					setShowResetSuccessModal(true);
					setActualPasswordError('');
					setConfirmationError('');
					updateUser_apiError('');
					authenticUser_ApiError('');
				},
				error: error => {
					console.log(error);
					if (error?.data?.errors?.old_password) {
						setActualPasswordError(error?.data?.errors?.old_password[0]);
					}
					setLoading(false);
					setShowResetSuccessModal(false);
				},
				complete: () => {
					setActualPassword('');
					setNewPassword('');
					setConfirmNewPassword('');
				},
			});
	};

	

	return (
		<div id="my-profile-settings" className={cls.host} style={{ padding: isMobile ? '10px' : '' }}>
			{data?.user?.id?.length > 0 && authenticatedUser.is_admin == true ? (
				<div style={{ width: '70%', display: 'flex', justifyContent: 'space-between' }}>
					<Button title={'Back'} clicked={() => navigate('/users')} style={{ width: '100px' }} />
					{data?.user && <Button title="Show Tasks" loading={loading} light clicked={() => setShowTasks(true)} />}
				</div>
			) : (
				<div style={{ margin: '80px 0 0 0', display: isMobile ? 'none' : '' }}>
					<HeaderTopBar headerTitle={'Change Account Password'} targetProcessing={false} />
				</div>
			)}

			

			<div className={cls.labeledContainerHost} style={{ width: isMobile ? '100%' : '50%' }}>
				
				<div className={cls.userDataWrapper}>
					<UnfocusableWrapper className={cls.userSettingsContainer} style={{ padding: isMobile ? '0' : '' }} onClickOutside={onSaveUserData}>
						
						<div style={{ display: isMobile ? '' : 'flex', gap: '24px', marginTop: '15px', width: '100%' }}>
							<div style={{ flex: 1 }}>
								<LabeledField label={data?.user?.id?.length > 0 && authenticatedUser.is_admin == true ? 'Admin Password' : 'Old Password'} error={fieldsErrors.first_name}>
									<input type="password" name="old-password" className={cls.inputField} value={actualPassword} onChange={e => setActualPassword(e.target.value)} />
								</LabeledField>
							</div>
							<div style={{ flex: 1 }}>
								<LabeledField label={'New Password'} error={fieldsErrors.first_name}>
									<input type="password" name="new-password" className={cls.inputField} value={newPassword} onChange={e => setNewPassword(e.target.value)} />
								</LabeledField>
							</div>
							<div style={{ flex: 1 }}>
								<LabeledField label={'Verify New Password'} error={fieldsErrors.last_name}>
									<input type="password" name="verify-password" className={cls.inputField} value={confirmNewPassword} onChange={e => setConfirmNewPassword(e.target.value)} />
								</LabeledField>
							</div>
						</div>
						<div style={{ display: 'flex' }}>
							{actualPassword.length > 5 && newPassword.length > 5 && confirmNewPassword.length > 5 && (
								<div style={{ display: 'flex', gap: '24px', marginTop: '15px', maxWidth: '100%' }}>
									<Button title="Update password" loading={loading} lighter clicked={onSubmitClick} />
								</div>
							)}
							
						</div>
						<div>{showResetSuccessModal && <p className={cls.success}>Password Successfully changed</p>}</div>
						<div>{actualPasswordError?.length > 0 && <p className={cls.errorGeneralText}>{actualPasswordError}</p>}</div>
						<div>{confirmationError?.length > 0 && <p className={cls.errorGeneralText}>{confirmationError}</p>}</div>
						<div>{authenticUser_ApiError?.length > 0 && <p className={cls.errorGeneralText}>{authenticUser_ApiError}</p>}</div>
						<div>{updateUser_apiError?.length > 0 && <p className={cls.errorGeneralText}>{updateUser_apiError}</p>}</div>
					</UnfocusableWrapper>
				</div>
			</div>
		</div>
	);
};
export const LabeledContainer = props => {
	const { label, children, readOnly } = props;
	return (
		<div className={cls.labeledContainerHost} style={{ width: isMobile ? '100%' : '50%' }} app-readonly={readOnly ? 'true' : 'false'}>
			<p className={cls.labeledContainerLabel}>{label}</p>
			{children}
		</div>
	);
};
const LabeledField = props => {
	const { label, error, children, wrappedContent } = props;
	return (
		<div className={cls.labeledFieldHost}>
			<label className={cls.labeledFieldLabel}>{label}</label>
			{wrappedContent ? <div className={cls.labeledFieldChildrenContainer}>{children}</div> : children}

			{error?.length > 0 && <p className={cls.labeledFieldError}>{error}</p>}
		</div>
	);
};

const mapStateToProps = store => {
	const { authenticatedUser } = store.authReducer;
	const { user } = store.sessionReducer;
	const { components } = store.flowReducer;
	return { authenticatedUser, user, components };
};

const mapDispatchToProps = dispatch => {
	return {
		onSetAuthenticatedUserPosition: position => dispatch(setAuthenticatedUser(position)),
	};
};
export default connect(mapStateToProps, mapDispatchToProps)(MyPassword);
